.under {
    margin-top: -10px;
    width: 50px;
    background-color: #000000;
    height: 4px;
    align-self: center;
}

.me .under {
    background-color: #FF006B;
}

.content {
    display: flex;
    flex-direction: row;
}

.photo {
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
    align-self: center;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
}

.me {
    text-align: center;
    padding: 10rem;
    display: flex;
    flex-direction: column;
    max-width: 20%;
}

.me p {
    text-align: center;
    font-family: 'Raleway', sans-serif;
}

h1 {
    font-family: 'Raleway', sans-serif;
    font-size: 36px;
    margin-bottom: 15px;
}